@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media all and (orientation: portrait) {
  .why-choose-easybank {
    padding: 90px 7% 100px 7%;
    background-color: rgb(244, 245, 247);
  }
  .reasons {
    text-align: center;
  }

  .img-reason {
    margin-top: 70px;
    width: 110px;
  }

  .h3-text {
    color: hsl(233, 26%, 24%);
    font-weight: 300;
    font-size: 30px;
    font-family: 'Public Sans';
    margin-top: 40px;
    margin-bottom: 35px;
  }
}
@media all and (orientation: landscape) {
  .p-text-style-default {
    color: hsl(233, 8%, 62%);
    line-height: 30px;
    font-weight: 400;
    text-align: left;
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 30px;
    width: 40%;
  }

  .why-choose-easybank {
    padding: 0 7%;
  }

  .reasons {
    margin-top: 60px;
    display: flex;
  }

  .img-reason {
    margin-bottom: 30px;;
  }

  .h3-text {
    color: hsl(233, 26%, 24%);
    font-weight: 300;
    font-size: 20px;
    font-family: 'Public Sans';
    margin-bottom: 20px;
  }

  .p-text-card-desktop {
    width: 95%;
  }
}
@media all and (orientation: landscape) {
  .articles {
    display: flex;
  }

  .image-currency {
    width: 90%;
    height: 200px;
  }

  .name-author {
    color: rgb(0, 0, 0, .5);
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 12px;
  }
  .h3-style-default {
    color: hsl(233, 26%, 24%);
    font-size: 20px;
    font-weight: 400;
    margin-bottom: 20px;
  }

  .p-article-style-default {
    color: rgb(0, 0, 0, .5);
    font-weight: 300;
    font-size: 14px;
    line-height: 28px;
    font-family: 'Public Sans';
  }

  .latest-articles {
    position: relative;
    top: -250px;
  }
}
@media all and (orientation: portrait) {
  .latest-articles {
    margin-bottom: 100px;
    background-color: rgb(250, 250, 250);
  }

  .h2-style-default {
    margin-top: 100px;
  }

  .card-article {
    margin-top: 50px;
    margin-bottom: 50px;
  }
  .image-currency {
    width: 100%;
  }
  
  .name-author {
    color: rgb(0, 0, 0, .5);
    margin-top: 40px;
    margin-bottom: 20px;
    font-size: 12px;
  }

  .h3-style-default {
    color: hsl(233, 26%, 24%);
    font-size: 22px;
    font-weight: 400;
    margin-bottom: 20px;
  }

  .p-article-style-default {
    color: rgb(0, 0, 0, .5);
    font-weight: 300;
    font-size: 17px;
    line-height: 28px;
    font-family: 'Public Sans';
  }
}
@media all and (orientation: portrait) {
  footer {
    padding-top: 50px;
    padding-bottom: 50px;
    background-color: rgb(45, 49, 78);
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .logo-footer {
    width: 200px;
    margin-bottom: 50px;
  }
  
  .ul-social-media {
    display: flex;
  }
  
  .ul-social-media li {
    padding: 0 10px;
  }
  
  .social-media {
    width: 30px;
    margin-bottom: 40px;
  }
  
  .menu-footer {
    color: rgb(255, 255, 255);
    text-align: center;
  }
  
  .menu-footer li {
    padding: 10px 0;
  }
  
  footer div > span {
    color: rgb(255, 255, 255, 0.5);
    font-size: 20px;
  }

  .menu-footer-desktop {
    display: none;
  }
}
.btn-request-invite {
  display: flex;
  align-self: center;
  padding: 10px 50px;
  border: none;
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  border-radius: 40px;
  background-color: rgb(28, 232, 124);
  background: linear-gradient(135deg, rgba(28, 232, 124, 1) 0%, rgba(28, 232, 124, 1) 38%, rgba(0, 212, 255, 1) 69%, rgba(0, 212, 255, 1) 100%);
}

@media all and (orientation: landscape) {
  footer {
    padding: 50px 7%;
    background-color: rgb(45, 49, 78);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .logo-footer {
    width: 200px;
    margin-bottom: 50px;
  }
  
  .ul-social-media {
    display: flex;
  }
  
  .ul-social-media li {
    padding: 0 10px;
  }
  
  .social-media {
    width: 30px;
    margin-bottom: 40px;
  }

  .btn-request-invite-all-rights-reserved {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100px;
  }

  .menu-footer {
    color: rgb(255, 255, 255);
    text-align: center;
  }
  
  .menu-footer li {
    padding: 10px 0;
  }
  
  footer div > span {
    color: rgb(255, 255, 255, 0.5);
    font-size: 20px;
  }

  .menu-footer-mobile {
    display: none;
  }

  .menu-footer-desktop {
    display: flex;
    position: relative;
    left: -10%;
    color: rgb(255, 255, 255);
  }

  .menu-footer-desktop ul {
    margin-left: 100px;
  }

  .menu-footer-desktop ul li {
    padding: 7px 0;
  }
}
@media all and (orientation: landscape) {

  .container {
    padding: 0 7%;
  }
  
  .label-menu-mobile {
    display: none;
  }

  .top-bar {
    background-color: rgb(255, 255, 255);
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 86%;
    height: 80px;
  }

  .logo {
    height: 20px;
  }

  .main-menu ul {
    display: flex;
    color: rgb(0, 0, 0, .6);
  }

  .main-menu ul li {
    padding: 0 15px;
  }

  .btn-request-invite-desktop {
    display: flex;
    align-self: center;
    padding: 10px 50px;
    border: none;
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    border-radius: 40px;
    background-color: rgb(28, 232, 124);
    background: linear-gradient(135deg, rgba(28, 232, 124, 1) 0%, rgba(28, 232, 124, 1) 38%, rgba(0, 212, 255, 1) 69%, rgba(0, 212, 255, 1) 100%);
  }

  .next-generation-digital-banking {
    background-image: url(../../static/media/image-mockups.48d35b17.png), url(../../static/media/bg-intro-desktop.8be308fd.svg);
    height: 130vh;
    background-position: 125%, 5900%;
    background-repeat: no-repeat;
  }

  .main-text-desktop {
    position: relative;
    bottom: 100vh;
    font-family: 'Public Sans';
  }

  .h2-style-default {
    font-size: 45px;
    text-align: left;
    color: hsl(233, 26%, 24%);
    font-weight: 300;
    margin-bottom: 30px;
    width: 40%;
  }

  .why-choose-easybank {
    position: relative;
    bottom: 350px;
  }

}
@media all and (orientation: portrait) {
  .label-menu-mobile {
    display: block;
  }
  .next-generation-digital-banking {
    background-image: url(../../static/media/image-mockups.48d35b17.png), url(../../static/media/bg-intro-mobile.c2ed38d0.svg);
    background-repeat: no-repeat;
    background-size: 100%;
    height: 100vh;
  }

  .btn-request-invite-desktop {
    display: none;
  }

  header {
    height: 80px;
    position: fixed;
    background-color: rgb(255, 255, 255);
    width: 86%;
  }

  .main-content-mobile {
    display: none;
  }

  .logo {
    height: 25px;
  }

  .top-bar {
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  [alt="icon-menu-hamburguer"] {
    width: 13px;
    height: 15px;
  }
  .main-menu {
    display: none;
    background-color: rgb(255, 255, 255);
    padding: 15px 37.5%;
    border-radius: 5px;
    box-shadow: rgb(0, 0, 0) 10px 30px 200px;
  }

  .main-menu ul li {
    padding: 13px 0;
  }

  /* .icon-menu-hamburguer {
    display: block;
  } */

  #input-menu-hamburguer:checked+nav {
    display: block;
    position: absolute;
    top: 100px;
    left: 7%;
  }
  .text {
    font-family: 'Public Sans';
  }
  .h2-style-default {
    font-size: 45px;
    text-align: center;
    color: hsl(233, 26%, 24%);
    font-weight: 300;
    margin-bottom: 30px;
  }
  .container {
      padding: 0 7%;
  }
  .p-text-style-default {
    color: hsl(233, 8%, 62%);
    line-height: 30px;
    font-weight: 400;
    text-align: center;
    font-size: 18px;
    line-height: 30px;
  }
  .btn-request-invite {
    display: flex;
    align-self: center;
    margin: 40px auto 40px auto;
    padding: 20px 50px;
    border: none;
    height: 60px;
    color: #fff;
    font-size: 20px;
    font-weight: bold;
    border-radius: 40px;
    background-color: rgb(28, 232, 124);
    background: linear-gradient(135deg, rgba(28, 232, 124, 1) 0%, rgba(28, 232, 124, 1) 38%, rgba(0, 212, 255, 1) 69%, rgba(0, 212, 255, 1) 100%);
  }
}
* {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

#input-menu-hamburguer {
  display: none;
}

.attribution { font-size: 11px; text-align: center; }
.attribution a { color: hsl(228, 45%, 44%); }

@media all and (orientation: portrait) {
  .main-content-desktop {
    display: none;
  }
}

@media all and (orientation: landscape) {
  .main-content-mobile {
    display: none;
  }
}

